<template>
  <div class="container">
    <img ref="image" :src="src" />
  </div>
</template>
<script>
import Cropper from "cropperjs";
export default {
  name: "index",
  components: {},
  props: {
    src: {
      type: [String, Object],
      required: true
    }
  },
  data() {
    return {
      cropper: null
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {
    const image = this.$refs.image;
    const cropper = new Cropper(image, {
      crop(event) {},
      viewMode: 1, // 
      dragMode: "move", // 
      aspectRatio: 1, // 
      autoCropArea: 1, // 
      cropBoxMovable: false, // 
      cropBoxResizable: false, // 
      modal: true, // 
      // movable: false
      background: false, // 
      movable: true // 
      // minContainerHeight: document.body.clientHeight, // 
      // minContainerWidth: 375 // 
    });
    this.cropper = cropper;
  },
  beforeDestroy() {},
  methods: {
    getCroppedBlob() {
      return new Promise(resolve => {
        this.cropper.getCroppedCanvas().toBlob(resolve);
      });
    }
  }
};
</script>
