<template>
  <van-picker
    title=""
    show-toolbar
    :columns="['', '']"
    default-index="0"
    @confirm="onConfirm"
    @cancel="$emit('close')"
    @change="onChange"
  />
</template>
<script>
import { updateUserProfile } from "network/my";
import { Picker, Toast } from "vant";
export default {
  name: "MyNamePopup",
  components: {
    [Picker.name]: Picker,
    [Toast.name]: Toast
  },
  props: {
    // gender: {
    //   type: Number,
    //   default: 0
    // }
  },
  data() {
    return {};
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  beforeDestroy() {},
  methods: {
    onConfirm(value) {
      // 
      Toast.loading({
        message: "...",
        forbidClick: true
      });
      if (value === "") {
        value = 1;
      } else {
        value = 0;
      }
      // 
      updateUserProfile({ gender: value }).then(res => {
        Toast.success("");
        // 
        this.$emit("input", value);
        this.$emit("close");
      });
    },
    onCancel() {},
    onChange() {}
  }
};
</script>
<style lang="less" scoped></style>
