import { request } from "utils/request";
/**
 * 
 */
export const getUserProfile = () => {
  return request({
    method: "GET",
    url: "/app/v1_0/user/profile"
  });
};

/**
 * 
 */
export const updateUserProfile = data => {
  return request({
    method: "PATCH",
    url: "/app/v1_0/user/profile",
    data
  });
};

/**
 * 
 */
export const updateUserPhoto = data => {
  return request({
    method: "PATCH",
    url: "/app/v1_0/user/photo",
    data
  });
};
