<template>
  <div>
    <van-nav-bar
      title=""
      left-text=""
      right-text=""
      @click-left="$emit('close')"
      @click-right="onComfirm"
    />
    <div class="field-wrap">
      <van-field
        v-model="localName"
        rows="2"
        autosize
        type="textarea"
        maxlength="7"
        placeholder=""
        show-word-limit
      />
    </div>
  </div>
</template>
<script>
import { updateUserProfile } from "network/my";
import { NavBar, Field } from "vant";
export default {
  name: "MyNamePopup",
  components: {
    [NavBar.name]: NavBar,
    [Field.name]: Field
  },
  props: {
    nickName: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      localName: this.nickName
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  beforeDestroy() {},
  methods: {
    onComfirm() {
      //   this.isNamePopupShow = false;
      // loading 
      this.$toast.loading({
        message: "",
        forbidclick: true // 
      });
      // 

      updateUserProfile({ name: this.localName }).then(res => {
        this.$toast.success("");
        // 

        this.$emit("input", this.localName);
        this.$emit("close");
      });
    }
  }
};
</script>
